$primaryColor: #4527a4;

// Common CSS
html {
	scroll-behavior: smooth;
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

.wp-block-mcb-mailchimp {
	width: 100%;
	display: flex;

	* {
		box-sizing: border-box;
	}

	.mcbMailChimp {

		.form {
			width: 100%;

			h2 {
				margin: 0px;
				padding: 0px;
				text-align: center;
				margin-bottom: 15px;
			}

			p {
				margin: 0px;
				padding: 0px;
				margin-bottom: 15px;
				text-align: center;
			}

			input:focus {
				outline: none;
				box-shadow: none;
			}

			input[type="text"],
			input[type="email"] {
				min-height: 40px;
				box-sizing: border-box;
				margin: 0;
				border: unset;
			}

			input[type="email"] {}

			.submitBtn {
				position: relative;

				button {
					border: none;
					cursor: pointer;
				}

				.loader {
					position: absolute;
					top: 0;
					width: 100%;
					height: 100%;
					background: #5d5c5cbf;
					text-align: center;
				}
			}

			.noticeElement {
				>p {
					margin: 0 auto;
					padding: 5px 10px;
					color: #fff;
					font-weight: 500;
				}
			}
		}

		&.default {
			.form {
				margin: 0 auto;
				border-radius: 5px;

				h2 {}

				p {}

				input:focus {}

				input[type="text"],
				input[type="email"] {
					width: 100%;
					min-height: 40px;
				}

				input[type="email"] {}

				.emailBtnEle {
					display: flex;
					column-gap: 5px;
				}

				.submitBtn {

					button {
						width: 100%;

					}

					.loader {}
				}

				.noticeElement {

					>p {}

					.warning {}

					.success {}
				}
			}
		}

		&.form1 {

			position: relative;
			padding: 0px;
			background-color: transparent;

			.iconElement {
				position: absolute;
				width: 100%;
				top: -35px;
				left: 0;
				display: flex;
				align-items: center;
				justify-content: center;

				.icon {
					position: relative;
					width: 70px;
					height: 70px;
					background: #fff;
					border-radius: 50%;
					display: flex;
					align-items: center;
					justify-content: center;

					&::after {
						content: '';
						position: absolute;
						top: 50%;
						left: 50%;
						width: 50px;
						height: 50px;
						transform: translate(-50%, -50%);
						border: 2px solid #000;
						border-radius: 50%;
					}
				}
			}

			.form {
				margin: 0 auto;
				border-radius: 5px;

				h2 {}

				p {}

				input:focus {}

				.emailBtnElement {
					display: flex;
					align-items: center;
					justify-content: center;
					column-gap: 5px;
					margin-top: 30px;

					@media all and (max-width: 575px) {
						flex-direction: column;
					}

					input[type="email"] {
						margin-bottom: 0px;
						width: 100%;
						flex: 1;
					}

					.submitBtn {
						@media all and (max-width: 575px) {
							width: 100%;
						}

						button {
							@media all and (max-width: 575px) {
								width: 100%;
								margin-top: 15px;
							}
						}

						.loader {}
					}
				}

				.noticeElement {

					>p {}

					.warning {}

					.success {}
				}
			}
		}

		&.form2 {
			.iconElement {

				.icon {
					margin-bottom: 20px;
					text-align: center;
				}
			}

			.form {
				margin: 0 auto;
				border-radius: 5px;

				h2 {}

				p {}

				input:focus {}

				.emailBtnElement {
					position: relative;
					margin-top: 30px;

					input[type="email"] {
						margin-bottom: 0px;
						width: 100%;
					}

					.submitBtn {
						position: absolute;
						top: 0;
						right: 0;

						@media all and (max-width: 575px) {
							position: unset;
							width: 100%;
						}

						button {
							@media all and (max-width: 575px) {
								width: 100%;
							}
						}

						.loader {}
					}
				}

				.noticeElement {

					>p {}

					.warning {}

					.success {}
				}
			}
		}

		&.form3 {
			.form {
				margin: 0 auto;
				border-radius: 5px;

				h2 {}

				p {}

				input:focus {}

				.nameElement {
					display: flex;
					align-items: center;
					justify-content: center;
					column-gap: 20px;
					margin-top: 25px;

					@media all and (max-width: 575px) {
						flex-direction: column;
					}

					input[type="text"] {
						width: 100%;
						min-height: 40px;
					}
				}

				input[type="email"] {
					width: 100%;
					margin: 0px;
				}

				.submitBtn {

					button {
						width: 100%;
					}

					.loader {}
				}

				.noticeElement {

					>p {}

					.warning {}

					.success {}
				}
			}
		}
	}

}